import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
  root: {
    // height: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'column',
    padding: '25px 30px',
    width: 400,
    backgroundColor: 'white',
    borderRadius: 8,
  //   '& .MuiInputBase-multiline': {
  //     margin: theme.spacing(1),
  //     width: '100%',
  //   // backgroundColor: 'blue',
  // }
  },
  formText: {
    margin: theme.spacing(1),
    width: '100%',
    // backgroundColor: "yellow",
  },
  formMultiText: {
    margin: theme.spacing(1),
    width: '100%',
    // backgroundColor: "yellow",
  },
}));

const FormButton = withStyles({
  root: {
    background: 'linear-gradient(90deg, rgba(255,0,0,1) 0%, rgba(205,32,32,1) 50%, rgba(158,6,29,1) 100%)',
    width: '100%',
    borderRadius: 3,
    border: 0,
    color: 'white',
    height: 48,
    padding: '0 30px',
    boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
  },
  label: {
    textTransform: 'capitalize',
  },
})(Button);

export default function ContactForm() {
  const classes = useStyles();

  return (
    <>
      <form className={classes.root} noValidate autoComplete="off">
          <TextField className={classes.formText} label="Nombre" variant="outlined" />
          <TextField className={classes.formText} label="Apellido" variant="outlined" />
          <TextField className={classes.formText} label="Email" variant="outlined" />
          <TextField className={classes.formText} label="Celular" variant="outlined" />
          <TextField className={classes.formMultiText}
          id="outlined-multiline-flexible"
          label="Multiline"
          multiline
          rowsMax={4}
          variant="outlined"
        />
          <FormButton type="submit">Enviar</FormButton>
      </form>
    </>
  );
}